<template>
  <div class="app-container">
    <!-- <v-breadcrumb></v-breadcrumb> -->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-select v-model="value" clearable placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select v-model="value" clearable placeholder="">
              <el-option
                v-for="item in status"
                :key="item.statusId"
                :label="item.label"
                :value="item.statusId">
              </el-option>
          </el-select>
        </el-form-item>
                <el-form-item >
          <el-input placeholder="" v-model="searchName"></el-input>
        </el-form-item>
        <el-form-item>
           <el-button type="primary" @click="doFilter()"><i class="el-icon-search"></i></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" ></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="tableList" v-loading="listLoading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="uid" label="">
      </el-table-column>
      <el-table-column prop="cname" label="">
      </el-table-column>
      <el-table-column prop="number" label="">
      </el-table-column>
      <el-table-column prop="date" label="">
      </el-table-column>
      <el-table-column  label="" width="120" >
        <template slot-scope="scope">
           <el-button size="small" :type="scope.row.status | statusFilter" @click="isStatus(scope.$index, scope.row)" v-if="scope.row.status == 1"></el-button>
          <el-button size="small"  :type="scope.row.status | statusFilter" @click="isStatus(scope.$index, scope.row)" v-if="scope.row.status == 2"></el-button>
        </template>
      </el-table-column>
      <el-table-column prop="operation" label=" ">
        <template slot-scope="scope" >
         <el-button size="small" type="primary"  @click="handleUpdate(scope.row)"></el-button>
          <el-button size="small" type="danger"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-pagination layout="total, prev, pager, next"
                    background
                    :page-size="10"
                    @size-change="handleSizeChange"
                    :total="total"
                    @current-change="handleCurrentChange"
                    style="text-align:center;">
    </el-pagination>


    <!--  -->
    <el-dialog title="Edit" :visible.sync="isShowEditVisible">
      <el-form label-width="80px" :model="temp" ref="dataForm">
        <el-form-item label="" prop="cname">
          <el-input v-model="temp.cname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-input v-model="temp.date"></el-input>
        </el-form-item>
        <el-form-item label="" v-model="temp.status">
         <el-select v-model="temp.status" placeholder="">
            <el-option v-for="item in status"
                       :label="item.label"
                       :value="item.statusId"
                       :key="item.statusId"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShowEditVisible = false"></el-button>
        <el-button type="primary" :loading="listLoading" @click="updateData" class="title1"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import breadcrumb from '@/components/breadcrumb/index'
import { getList, updateArticle } from '@/api/table'
export default {
    name: 'userMan',
  data() {
    return {
      tableList: [],
      listLoading: true,
      isShowEditVisible: false,
      temp: {
        uid: '',
        cname: '',
        date: '',
        status: ''
      },
      total: 0,
      page: 1,
      pageSize: 10,
      status: [
        {
          statusId: 1,
          label: ''
        }, {
          statusId: 0,
          label: ''
        }
      ],
      options: [
        {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
      value: '',
      searchName: '',
      filterTableDataEnd: []
    }
  },
  created() {
    this.fetchData()
  },
  components:{

      'v-breadcrumb':breadcrumb
  },
  filters: {
    statusFilter(status) {
      const statusMap = {
        1: 'success',
        2: 'danger'
      }
      return statusMap[status]
    }
  },
  methods: {
    fetchData() {
      this.listLoading = true
      getList(this.listQuery).then(response => {
        const limit = 10;
        debugger
        const pageList = response.data.filter((item, index) =>index < limit * this.page && index >= limit * (this.page - 1))
        console.log(pageList)
        this.total = response.data.length
        this.tableList = pageList
        this.listLoading = false
      })
    },
    doFilter() {
      if (this.searchName === '') {
        this.fetchData()
        // this.$message.warning('')
        return
      }
      console.log(this.searchName)
      // ,
      this.filterTableDataEnd = []
      this.tableList.forEach((value, index) => {
        if (value.cname) {
          if (value.cname.indexOf(this.searchName) >= 0) {
            this.filterTableDataEnd.push(value)
            console.log(this.filterTableDataEnd)
          }
        }
      })
      // 
      this.page = 1
      this.total = this.filterTableDataEnd.length
      // ,
      this.currentChangePage(this.filterTableDataEnd)
    },
    clickfun(e) {
      console.log(e.target.innerText)
    },
    handleUpdate(row) {
      this.isShowEditVisible = true
      this.temp = Object.assign({}, row)
      console.log(row)
    },
    updateData() {
      const tempData = Object.assign({}, this.temp)
      console.log(tempData)
      // updateArticle(tempData).then(() => {
      //   for (const v of this.tableList) {
      //     if (v.uid === this.temp.uid) {
      //       const index = this.tableList.indexOf(v)
      //       this.tableList.splice(index, 1, this.temp)
      //       break
      //     }
      //   }
      //   this.isShowEditVisible = false
      //   this.$notify({
      //     title: '',
      //     message: '',
      //     type: 'success',
      //     duration: 2000
      //   })
      // })
    },
    handleSizeChange(val) {
      this.page = val
      console.log(this.page)
      this.fetchData()
    },
    handleCurrentChange(val) {
      this.page = val
      console.log(this.page)
      this.fetchData()
    },
    currentChangePage(list) {
      let from = (this.page - 1) * this.pageSize
      const to = this.page * this.pageSize
      this.tableList = []
      for (; from < to; from++) {
        if (list[from]) {
          this.tableList.push(list[from])
        }
      }
    }
  }
}
</script>



